*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --fontPrimary: "Poppins", sans-serif;
  --fontSecondary: "Roboto", sans-serif;
  --card-grid-item: 1;
  --card-grid-width: 20px;
}

.theme-light {
  --black: #202025;
  --grapefruit: #fd5103;
  --gray: #5f6775;
  --white: #fcfcfc;
  --auxOrange: #fe7031;
}

.theme-dark {
  --black: #fff;
  --grapefruit: #fd5103;
  --gray: #5f6775;
  --white: #202025;
  --auxOrange: #fe7031;
}

body {
  font-family: var(--fontPrimary);
  color: var(--black);
  background-color: var(--white);
}

body.modal-open {
  overflow: hidden;
}

h1, h2, h3, h4, h5, p, ul {
  margin: 0;
  padding: 0;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 100vw;
  }
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 33px;
    padding-right: 33px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 15px;
    padding-right: 15px;
  }
}

ul {
  text-decoration: none;
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
}

.card-set {
  flex-wrap: wrap;
  gap: 20px;
  display: flex;
}

@media (min-width: 768px) {
  .card-set {
    --card-grid-item: 2;
    --card-grid-width: 30px;
    gap: 30px;
  }
}

@media (min-width: 1280px) {
  .card-set {
    --card-grid-item: 3;
  }
}

.card-set__item {
  flex-basis: calc((100% - (var(--card-grid-item)  - 1) * var(--card-grid-width) ) / var(--card-grid-item) );
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  display: none;
}

.logo-cocktails {
  display: block;
}

.header__container {
  padding-top: 20px;
  display: flex;
  position: relative;
}

.desctop-wrapper {
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  margin-left: 43px;
  display: flex;
}

.navigation-wrapper {
  display: flex;
}

.features-wrapper {
  gap: 100px;
  display: flex;
}

.features-wrapper .btn-login {
  border: 1px solid var(--grapefruit);
  color: var(--grapefruit);
  background: none;
  border-radius: 4px;
  margin-left: 10px;
  padding: 10px;
}

.search__form {
  position: relative;
}

.search__form .search__icon {
  position: absolute;
  top: 10px;
  left: 10px;
}

.search__form:before {
  content: "";
  width: 1px;
  height: 40px;
  background-color: var(--grapefruit);
  position: absolute;
  top: 0;
  transform: translateX(40px);
}

.search__input {
  height: 40px;
  width: 100%;
  border: 1px solid var(--grapefruit);
  background: none;
  border-radius: 4px;
  outline: none;
  padding: 8px 0 8px 48px;
}

.checkbox__wrapper {
  margin-bottom: 40px;
}

.theme-wrapper {
  justify-content: flex-end;
  align-items: center;
  gap: 12px;
  display: flex;
}

.theme-name__light, .theme-name__dark {
  color: var(--grey);
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.17;
  transition-duration: .25s;
  transition-timing-function: ease-in-out;
}

.theme-acctive {
  color: var(--grapefruit);
}

.label {
  background-color: var(--white);
  border: 1px solid var(--grapefruit);
  height: 20px;
  width: 40px;
  cursor: pointer;
  border-radius: 50px;
  display: flex;
  position: relative;
}

.checkbox {
  opacity: 0;
  position: absolute;
}

.checkbox:checked + .label .ball {
  background-color: var(--black);
  transform: translateX(20px);
}

.checkbox:checked + .label {
  background-color: var(--grapefruit);
  border: 1px solid var(--white);
}

.ball {
  height: 16px;
  width: 16px;
  background-color: var(--grapefruit);
  border-radius: 50%;
  transition-duration: .25s;
  transition-timing-function: ease-in-out;
  position: absolute;
  top: 1px;
  left: 1px;
}

.favorite-acctive {
  cursor: pointer;
  background: none;
  border: none;
  padding-left: 0;
  padding-right: 0;
  position: relative;
}

.favorite-wrapper {
  width: 210px;
  border: 1px solid var(--grapefruit);
  background: var(--white);
  opacity: 1;
  border-radius: 4px;
  flex-direction: column;
  margin-top: 8px;
  transition-duration: .25s;
  transition-timing-function: ease-in-out;
  display: flex;
  position: absolute;
}

.favorite-link {
  color: var(--grey);
  padding: 12px;
  font-size: 18px;
  transition-duration: .25s;
  transition-timing-function: ease-in-out;
}

.favorite-link:hover, .favorite-link:focus {
  color: var(--white);
  background: var(--grapefruit);
}

.favorite-wrapper__close {
  opacity: 0;
  pointer-events: none;
  transform: translateY(-20px);
}

@media (max-width: 767.98px) {
  .navigation-wrapper, .theme-wrapper {
    display: none;
  }

  .desctop-wrapper {
    justify-content: flex-end;
    margin-left: 0;
  }

  .search__form {
    display: none;
  }

  .search__form.active {
    z-index: 3;
    display: flex;
    position: absolute;
    top: 100px;
    left: 20px;
  }

  .theme-wrapper.active {
    z-index: 3;
    display: flex;
    position: absolute;
    top: 30px;
    left: 20px;
  }

  .navigation-wrapper.active {
    z-index: 3;
    display: flex;
    position: absolute;
    top: 171px;
    left: 20px;
  }

  .navigation-wrapper.active .desc-menu__link {
    flex-direction: column;
  }
}

@media (min-width: 768px) and (max-width: 1279.98px) {
  .header__logo {
    z-index: 10;
  }

  .search__form.active {
    z-index: 1;
    display: none;
  }

  .theme-wrapper.active {
    z-index: 3;
    display: flex;
    position: absolute;
    top: 30px;
    left: 213px;
  }

  .navigation-wrapper.active {
    z-index: 3;
    display: flex;
    position: absolute;
    top: 114px;
    left: 326px;
  }

  .navigation-wrapper.active .desc-menu__link {
    flex-direction: column;
    gap: 0;
  }
}

