}

.backdrop {
  z-index: 10000;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .2);
  transition-property: opacity, visibility;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  pointer-events: none;
  opacity: 0;
}

.modal-wrapper {
  width: 100%;
  flex-flow: column wrap;
  display: flex;
